@use "variables.scss";

.field__input {
  font-family: "PT Sans", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #000000;
}

.field__input--transparent {
  color: #0000004d;
}

@media (min-width: variables.$tablet-width) {
  .field {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
  }
}
